import { GridCellPosition } from '../utils/grid/Grid';
import { checkRect } from './checkRect';
import { PIKeys } from '../constants';
import type { Float32BufferAttribute, BufferAttribute } from 'three';
import { useSpring, animated } from '@react-spring/three';
const GUTTER = 0.02;
const sliceX = vSlice('_0', '_2')(1, GUTTER);
const sliceY = vSlice('_1_4', '_1_2')(1, GUTTER);

type GlobeCellGeometryProps = {
  rect: GridCellPosition;
  radius?: number;
};

type GlobeCellGeometryState = {
  prev?: GridCellPosition;
  geometry?: SphereBufferGeometry;
  prevPositionGeometry?: BufferAttribute;
};
type SliceRange = [PIKeys, PIKeys];
export class GridGlobeCellGeometry extends Component<GlobeCellGeometryProps, GlobeCellGeometryState> {
  state: GlobeCellGeometryState = {
    // prev: [-1, -1, 0, 0] as GridCellPosition,
  };
  static defaultProps: GlobeCellGeometryProps = {
    rect: [0, 0, 1, 1] as GridCellPosition,
    radius: 1000,
  };

  componentDidUpdate(previous: GlobeCellGeometryProps) {
    if (!checkRect(this.props.rect)(previous.rect)) {
      // this.geometryShuffle({ prev: previous.rect! });
      this.makeGeometry();
    }
  }
  // shouldComponentUpdate(next: GlobeCellGeometryProps) {
  //   return !this.state.geometry || !checkRect(this.props.rect)(next.rect);
  // }
  static DEFAULT_GUTTER = 0.02;

  static rectSlicer(sliceH: Slicer, sliceV = sliceH) {
    return ([x, y, w, h]: GridCellPosition): GridCellPosition => {
      return [
        ...sliceH(x, w),
        ...sliceV(y, h),
      ];
    };
  }
  static geomFactory(hRange: SliceRange, wRange: SliceRange, gutter = this.DEFAULT_GUTTER) {
    const slicer = this.rectSlicer(
      vSlice(hRange[0], hRange[1], 'POINT_SCALAR')(1, gutter),
      vSlice(wRange[0], wRange[1], 'POINT_SCALAR')(1, gutter),
    );

    return (rect: GridCellPosition, radius = 1, numW = 9, numH = numW) => {
      const angles = slicer(rect);
      console.log(rect, angles);
      console.log(...angles.concat(...rect).map(n => n.toFixed(4)));
      const [ps, pl, ts, tl] = angles;
      return new SphereBufferGeometry(radius, numW, numH, ps);
    };
  }

  static defaultFactory = GridGlobeCellGeometry.geomFactory(['_1', '_2'], ['_1_4', '_1_2']);
  // static defaultEmpty = GridGlobeCellGeometry.defaultFactory([0, 0, 1, 1]);

  // private geometryShuffle(otherState?: GlobeCellGeometryState) {
  //   const { prev, geometry } = this.state;
  //   const oUpdate = otherState ?? {};
  //   if (geometry) {
  //     this.setState({
  //       ...oUpdate,
  //       prevPositionGeometry: geometry.getAttribute('position').clone(),
  //       geometry: GridGlobeCellGeometry.defaultFactory(this.props.rect, this.props.radius),
  //     }, () => {
  //       geometry.morphAttributes = {};
  //       geometry.dispose();
  //       const newGeom =  this.state.geometry;
  //       if (newGeom) {
  //         if (this.state.prevPositionGeometry) {
  //           if (!newGeom.morphAttributes.position) {
  //             newGeom.morphAttributes.position = [];
  //           }
  //           newGeom.morphAttributes.position[0] = this.state.prevPositionGeometry;
  //         }
  //       }
  //     });
  //   } else {
  //     this.setState({
  //       ...oUpdate,
  //       prevPositionGeometry: GridGlobeCellGeometry.defaultEmpty.getAttribute('position').clone(),
  //       geometry: GridGlobeCellGeometry.defaultFactory(this.props.rect, this.props.radius),
  //     }, () => {
  //       const newGeom =  this.state.geometry;
  //       if (newGeom) {
  //         if (this.state.prevPositionGeometry) {
  //           if (!newGeom.morphAttributes.position) {
  //             newGeom.morphAttributes.position = [];
  //           }
  //           newGeom.morphAttributes.position[0] = this.state.prevPositionGeometry;
  //         }
  //       }
  //     });
  //   }
  // }

  componentDidMount() {
    // this.geometryShuffle();
    this.makeGeometry();
  }

  componentWillUnmount() {
    const { geometry } = this.state;
    if (geometry) {
      geometry.morphAttributes = {};
      geometry.dispose();
    }
    this.#geoTo.dispose();
    this.#geoFrom.dispose();
  }
  makeGeometry() {
    const old = this.#geoFrom;
    this.#geoFromBuff = this.#geoTo.getAttribute('position').clone();
    this.#geoTo = GridGlobeCellGeometry.defaultFactory(this.props.rect, this.props.radius);
    this.#geoTo.morphAttributes.position = this.#geoTo.morphAttributes.position || [];
    this.#geoTo.morphAttributes.position[0] = this.#geoFromBuff;
    old.dispose();
  }

  #geoTo = GridGlobeCellGeometry.defaultFactory([0, 0, 1, 1], this.props.radius);
  #geoFrom = GridGlobeCellGeometry.defaultFactory([0, 0, 1, 1], this.props.radius);
  #geoFromBuff = this.#geoFrom.getAttribute('position').clone();
  render() {
    return (
      <primitive
        attach="geometry"
        object={this.#geoTo}
        ></primitive>
    );
  }
}


const sl = GridGlobeCellGeometry.rectSlicer(
  vSlice('_1', '_2', 'POINT_SCALAR')(1, GUTTER),
  vSlice('_1_4', '_1_2', 'POINT_SCALAR')(1, GUTTER),
);
export function GridGlobeGeometry({ rect = [0.5, 0.5, 0, 0] }: { rect: GridCellPosition }) {
  const l = useImgArrayImgsLength();
  const n = useMemo(() => 2 << (7 - Math.log(l as number)), [l]);
  const buffer = useBufferHeight();
  // const [x, y, w, h] = rect;
  // const [xD, wD] = sliceX(x, x + w);
  // const [yD, hD] = sliceY(y, y + h);

  const ref = useRef<SphereGeometry>();

  const sphereSlice = sl(rect);
  // const { pS } = useSpring({ pS: sphereSlice[0], config: { tension: 100, friction: 20, mass: 10 } });
  return (
    <animated.sphereGeometry
      args={[buffer.diag * 2.4, n, n, ...sphereSlice]}
      ref={ref}
      >
    </animated.sphereGeometry>
  );
}
