/* eslint-disable @typescript-eslint/no-explicit-any */
import { ColConstraint, CompleteGridCell, GridConstraints, IGridCell, PositionedCell } from './IGridCell';
import GridWorker from './GridWork.worker';
import { Grid } from "./Grid";
import { CreateGridMessage, GridWorkerMessageTypes } from "./WorkerTypes";
import { GridCol } from './GridCol';

function assignWorker(klass: { worker?: Worker }, worker: Worker = new GridWorker()): asserts klass is { worker: Worker } {
  klass.worker = worker;
}
export class WorkerGrid<T> extends Grid<T> {
  static worker?: Worker;
  static getWorker() {
    if (!this.worker) {
      this.worker = new GridWorker();
      assignWorker(this, this.worker);
      this.worker.addEventListener('message', e => {
        const { id, jobId, type, ...otherData } = e.data;
        const grid = this.trackedGrids.get(id);
        // console.log(id, type, jobId, grid, otherData);
        if (type === GridWorkerMessageTypes.JOB_DONE) {
          grid?._hydrate(otherData.object);
          const resolveJob = this.gridJobs.get(jobId);
          resolveJob && resolveJob(otherData.results);
        }
        if (type === GridWorkerMessageTypes.CELL_RECT_UPDATE) {
          // debugger;
          grid?.notifyCellSubscribers(otherData.key, otherData.rect);
        }
        grid?._jobQueue.delete(jobId);
      });
    }
    return this.worker;
  }

  static issueID() {
    return parseInt(Math.abs((Date.now() << 24) + Math.random()).toString(2).split('.').join(''), 2);
  }

  static trackedGrids = new Map<number, WorkerGrid<unknown>>();
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  static gridJobs = new Map<number, (result: any) => Promise<any>>();
  static create<T>(constrain: GridConstraints, constraintValue: ColConstraint) {
    const grid = new WorkerGrid<T>(constrain, constraintValue);
    const id = grid.id;
    this.trackedGrids.set(id, grid as WorkerGrid<unknown>);
    const jId = this._makeMessage(GridWorkerMessageTypes.GRID_CREATE, grid, { grid: [ constrain, constraintValue ] });
    grid._jobQueue.delete(jId);
    const cellSet = new Set<T>();
    grid.subscribeGlobal((r, k) => {
      if (typeof k === 'undefined') return;
      const s = cellSet.size; /*console.log(cellSet);*/
      if (r[0] === 1 && r[1] === 1) { cellSet.delete(k) }
      else { cellSet.add(k) }
      /*console.log(k, grid.length, s, cellSet.size,  cellSet, ...r); */
      if (s !== cellSet.size || s !== grid.length) grid._needsResizing = true;
    });
    return grid;
  }

  static async waitForWorker<T>(grid: WorkerGrid<T>, fnName: keyof WorkerGrid<T>, args: any[]): Promise<unknown> {
    return new Promise((resolve) => {
      this._makeMessage(GridWorkerMessageTypes.GRID_JOB, grid, { fnName, args }, resolve);
    });
  }
  static pushToWorker<T>(grid: WorkerGrid<T>, fnName: keyof WorkerGrid<T>, args: any[]) {
    const jId = this._makeMessage(GridWorkerMessageTypes.GRID_JOB, grid, { fnName, args });
    return jId;
    // const jobId = this.issueID();
    // const worker = this.getWorker();
    // worker?.postMessage({
    //   id: grid.id,
    //   jobId,
    //   fnName,
    //   args,
    // });
    // grid._jobQueue.add(jobId);
  }

  private static _makeMessage<T>(type: GridWorkerMessageTypes, grid: WorkerGrid<T>, data: any, listener?: ((value: any) => any) | null | undefined) {
    const jobId = this.issueID();
    this.getWorker()?.postMessage({
      ...data,
      type,
      id: grid.id,
      jobId,
    });
    grid._jobQueue.add(jobId);
    listener && this.gridJobs.set(jobId, (result) => Promise.resolve(result).then(listener));
    return jobId;
  }

  static returnToSender() {
    //
  }
  readonly id: number = WorkerGrid.issueID();
  _jobQueue = new Set<number>();

  _hydrate(otherAttributes: Grid<T>) {
    otherAttributes.cols.forEach(col => {
      const localCol = new GridCol<T>(col.index);
      localCol.cloneFrom(col);
      this.cols[col.index] = localCol;
    });
    // console.log(this.cols);
    for (const cell of otherAttributes.cells.values()) {
      const cCell = cell as CompleteGridCell<T>;
      this.storeLocal(cCell);
      // cCell.col = this.cols[cCell.col.index];
    }
    const { constrain, constraintValue, isWaitingForResize = false } = otherAttributes;
    Object.assign(this, { constrain, constraintValue, _needsResizing: isWaitingForResize });
  }

  push(cell: IGridCell<T>): void {
    this._needsResizing = this._needsResizing || !this.cells.has(cell.key);
    WorkerGrid.pushToWorker(this, 'push', [cell]);
    // this.storeLocal(cell);
  }
  remove(cellKey: T): void {
    WorkerGrid.pushToWorker(this, 'remove', [cellKey]);
    this.clearCell(cellKey);
    // this.storeLocal(cell);
  }

  resize() {
    WorkerGrid.waitForWorker(this, 'resize', []).then(() => {
      this._needsResizing = false;
    });
  }

  setContraint(constraint: ColConstraint) {
    super.setContraint(constraint);
    WorkerGrid.pushToWorker(this, 'setContraint', [constraint]);
  }

  async waitForArray(): Promise<PositionedCell<T>[]> {
    if (this.isCached()) return Promise.resolve(this.getCachedAndNotify());

    return (WorkerGrid.waitForWorker(this, 'toArray', [this._needsResizing]) as Promise<PositionedCell<T>[]>).then(result => {
      this._toMatrixCache = result;

      return result;
    });
  }
}
