type GridStateStore = {
  grid: WorkerGrid<string>
};

export const useGridStore = create<GridStateStore>(() => ({
  grid: WorkerGrid.create<string>(GridConstraints.MAX_COLS, [12]),
}));

const _pluckGrid = pluck('grid');
export const useGrid = () => useGridStore(_pluckGrid);
