import { ReactNode, useLayoutEffect, useState } from 'react';
import { WorkerGrid } from '../utils/grid/WorkerGrid';
import { useRefEffect } from '../utils/useRefEffect';
import { PositionedCell } from '../utils/grid/IGridCell';
import { useRefLayoutEffect } from '../utils/useRefLayoutEffect';
import { useThree } from '@react-three/fiber';
import { useGrid } from '../state/grid';

export function GridRender({ afterUpdate, children = null }: { afterUpdate?(): void; children?: ReactNode; }) {
  const [cells, setCells] = useState<PositionedCell<string>[]>([]);
  const regress = useThree((state) => state.performance.regress);
  const grid = useGrid();
  // const [isLoaded, setLoaded] = useState(!gridNeedsUpdate);
  useLayoutEffect(() => {
    if (children && Array.isArray(children) && children.length === cells.length){
      if (!grid.isWaitingForResize) {
        return;
        // if (cells.length === grid.length)
        // return;
      }}
    regress();
    let isUnmounted = false;
    grid.waitForArray().then(c => {
      if (isUnmounted)
        return;
      if (c.length !== cells.length || c.some((c1, i) => cells[i].cell.key !== c1.cell.key)) {
        setCells(c);
        // setLoaded(true);
        afterUpdate && afterUpdate();
      }
    });
    return () => {
      isUnmounted = true;
    };
  }, [afterUpdate, cells, children, grid, regress]);

  return (
    <>
      {children}
    </>
  );
}
