import { useCallback, useRef, useState, useMemo, useLayoutEffect } from 'react';
import { GridConstraints } from '../utils/grid';
import { Group } from 'three/src/objects/Group';
import { WorkerGrid } from '../utils/grid/WorkerGrid';
import { GridGlobeCell } from './GridGlobeCell';
import { GridRender } from './GridRender';
import { useRefEffect } from '../utils/useRefEffect';
import { GlobeProps } from '@anonymous.club/types';
import { useImgArrayImgs } from '../state/imgArray';
import { useTrail } from '@react-spring/three';
import { useRefLayoutEffect } from '../utils/useRefLayoutEffect';
import { useGrid } from '../state/grid';
import { MathUtils } from 'three';

export function GridGlobe(props: GlobeProps) {
  const imgs = useImgArrayImgs();
  const { cols = 4 } = props;
  const numTails = (imgs.length >> 3) + 1;
  const numCols = Math.min(imgs.length, cols);
  const [trail, api] = useTrail(numTails, (i) => ({ from: { morphInfluence: 0 }, to: { morphInfluence: 1 }, config: { duration: MathUtils.mapLinear(i, 0, numTails, 200, 550), delay: 10 } }));
  // const gridRef = useRef(WorkerGrid.create<string>(GridConstraints.MAX_COLS, [cols]));
  const grid = useGrid();
  // useLayoutEffect(() => grid.subscribeGlobal((r, k) => {console.log(...r, k?.split('/').slice(-1)[0])}), []);
  const groupRef = useRef<Group>();
  const [gridNeedsUpdate, setGridUpdate] = useState(false);
  const markGridForUpdate = useCallback(() => {
    setGridUpdate(true);
    grid.waitForArray();
    api.start((i) => ({ to: [{ morphInfluence: MathUtils.randFloat(1 / (i + 1), 0.8) }, { morphInfluence: 1 }], config: { duration: 200 } }));
  }, [api, grid]);
  useLayoutEffect(() => {
    if (grid.constraintValue[0] !== numCols) {
      grid.setContraint([numCols]);
      grid.resize();
      setGridUpdate(true);
    }
    // api.pause();
  }, [numCols, grid, grid.length]);
  // useRefEffect(([grid]) => {

  //   grid.resize();
  // }, [gridRef], [cols]);
  return (
    <group
      ref={groupRef}
      scale-z={-1}
      >
      {/* <group>
        {imgs.map((src) => (
          <GridGlobeCell
            afterImageLoad={markGridForUpdate}
            gridRef={gridRef}
            key={src}
            src={src}
            ></GridGlobeCell>
            ))}
      </group> */}
      <GridRender
        afterUpdate={useCallback(() => {
          setGridUpdate(false);
          api.start({ morphInfluence: 1 });
          // api.start({ from: { morphInfluence: 0.6 }, to: { morphInfluence: 0.3 } });
        }, [api])}
        >
        {useMemo(() => imgs.map((src, i) => (
          <GridGlobeCell
            afterImageLoad={markGridForUpdate}
            key={`${grid.id}/${src}`}
            // key={`${grid.id}/${src}/${i}/${imgs.length}/${cols}`}
            morph={trail[i % numTails].morphInfluence}
            src={src}
            ></GridGlobeCell>
          )), [cols, grid.id, imgs, markGridForUpdate, numTails, trail])}
      </GridRender>
    </group>
  );
}
export default GridGlobe;
