import { useCallback, useEffect, useLayoutEffect, useMemo, useRef, useState } from 'react';
import { Grid } from "../utils/grid";
import { GridCellPosition } from '../utils/grid/Grid';
import { ImageMaterial } from './ImageMaterial';
import { useRefEffect } from '../utils/useRefEffect';
import type { Mesh } from 'three/src/objects/Mesh';
import { verticalSlice } from './verticalSlice';
import { GridGlobeCellGeometry, GridGlobeGeometry } from './GridGlobeGeometry';
import { checkRect } from './checkRect';
import { useBufferHeight, useRefFrame } from '../utils';
import { SpringValue, animated } from '@react-spring/three';
import { useRefLayoutEffect } from '../utils/useRefLayoutEffect';
import { useGrid } from '../state/grid';
import { ImageDimensions } from '@anonymous.club/types';
import { useThree } from '@react-three/fiber';
import { useImgArrayRemovals } from '../state/imgArray';

function CellInside({ src, afterImageLoad }: { src: string; afterImageLoad?(image: ImageData): void; }) {
  const grid = useGrid();
  const [rect, setRect] = useState<GridCellPosition>(() => (new Float64Array(4) as unknown as GridCellPosition));
  const buffer = useBufferHeight();

  const rCheck = useMemo(() => checkRect(rect), [rect]);

  useLayoutEffect(() => {
    let isUnmounted = false;
    const unsub = grid.subscribeToCell(src)((r) => {
      if (isUnmounted || rCheck(r)) return;

      // setRect(r);
      rect[0] = r[0];
      rect[1] = r[1];
      rect[2] = r[2];
      rect[3] = r[3];
      setRect(rect.slice() as GridCellPosition);
    });
    return () => {
      isUnmounted = true;
      unsub();
    };
  }, [src, rect[0], rect[1], rect[2], rect[3], grid]);
  const sRect = rect || new Array(4);
  const onLoad = useCallback((image: ImageData) => {
    // if (rect[2] !== 0 && rect[3] !== 0) return;

    afterImageLoad && requestAnimationFrame(() => afterImageLoad(image));
  }, [afterImageLoad]);
  // }, [afterImageLoad, rect, sRect[0], sRect[1], sRect[2], sRect[2]]);
  const imgM = useMemo(() => (
    <ImageMaterial
      src={src}
      onLoad={onLoad}
      />
  ), [onLoad, src]);

  return (
    <>
      {useMemo(() => (
        <GridGlobeGeometry
          // radius={buffer.diag * 2.4}
          rect={rect}
          />
      ), [rect[0], rect[1], rect[2], rect[3]])}
      {imgM}
    </>
  );
}
export function GridGlobeCell({ src, afterImageLoad, morph }: { src: string; afterImageLoad?(image: ImageDimensions): void; morph?: SpringValue<number>; }) {
  const removedSet = useImgArrayRemovals();
  const grid = useGrid();
  const regress = useThree((state) => state.performance.regress);
  useEffect(() => {
    return () => {
      if (!removedSet.has(src)) return;
      regress();
      grid.remove(src);
      // requestAnimationFrame(() => grid.remove(src));
      removedSet.delete(src);
    };
  }, [grid, regress, removedSet, src]);
  const onLoad = useCallback((image: ImageDimensions) => {
    regress();
    requestAnimationFrame(() => grid.push({ key: src, height: image.height, next: undefined }));
    afterImageLoad && requestAnimationFrame(() => afterImageLoad(image));
  }, [afterImageLoad, grid, regress, src]);

  const ref = useRef<Mesh>();
  // useRefEffect(([mesh]) => {
  //   if (!mesh.morphTargetInfluences) {
  //     mesh.morphTargetInfluences = [];
  //     mesh.morphTargetDictionary = {};
  //     mesh.updateMorphTargets();
  //   }
  // }, [ref], [src]);
  const inf = useRef<number[]>([]);
  // useRefFrame(([mesh]) => {
  //   if (morph && mesh.morphTargetInfluences && mesh.morphTargetInfluences[0]) {
  //     mesh.morphTargetInfluences[0] = morph.get();
  //   }
  // }, [ref]);
  return (
    <animated.mesh
      castShadow
      receiveShadow
      // dispose={null}
      morphTargetInfluences={inf.current}
      morphTargetInfluences-0={0}
      ref={ref}
      scale={morph}
      >
      {useMemo(() => (<CellInside {...{ src, afterImageLoad: onLoad }} />), [onLoad, src])}
    </animated.mesh>
  );
}
